.breed-select-container {
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
}

.slim-select {
  width: 100%;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.slim-select-dropdown {
  max-height: 200px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-top: none;
  border-radius: 0 0 5px 5px;
  overflow-y: auto;
}

.slim-select-optgroup, .slim-select-option {
  cursor: pointer;
  padding: 10px;
  transition: background-color .2s;
}

.slim-select-optgroup:hover, .slim-select-option:hover {
  background-color: #f0f0f0;
}

.slim-select-option.active {
  color: #fff;
  background-color: #007bff;
}

.slim-select-search {
  border: none;
  border-bottom: 1px solid #ccc;
  padding: 10px;
  font-size: 16px;
}

.slim-select-search-status {
  color: #999;
  padding: 10px;
}

.loader, .error {
  color: #333;
  margin-top: 10px;
  font-size: 18px;
  font-weight: bold;
  display: none;
}

